.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 60px;
}

.pokemon-list {
  display: grid;
  grid-template-columns: repeat(4,200px);
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
}

.pokemon-list a {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  text-decoration: none;
  color: #333;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    padding: 10px;
    background-color: lightgrey;
    color: 222px;
    border-radius: 5px;
}



  
.pokemon-list-header{
    height: 30px;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    display: flex;
    justify-content: space-around;
    margin-top: 10px;

  }

  button{
    height: 100%;
  }
